:root {
  --hero-background-light-color: #141f8a70;
  --hero-background-middle-color: #192c93;
  --hero-background-dark-color: #182776;
  --acent-color: #fe3e57;
  --on-primary-color: #fff;
  --primary-dark-color: #071666;
  --chip-color-1: #20c7ec;
  --chip-color-2: var(--acent-color);
  --chip-color-3: #03fe95;
}

body {
  color: var(--on-primary-color);
}

h2 {
  font-size: 62px;
  margin: 0;
}

.pageSection {
  width: 100%;
}

.pageSection > .container {
  min-height: 100vh;
  width: 75%;
  margin: 0 auto;
  position: relative;
}

.pageSection .title {
  margin: 0;
  padding-top: 56px;
  width: 100%;
  text-align: center;
}

.acentColor {
  color: var(--acent-color);
}

.pageSection:nth-child(2n + 1) {
  background: var(--primary-dark-color);
  box-shadow: inset 0px 4px 149px #00000040;
}

.pageSection:nth-child(2n + 4) {
  background: var(--hero-background-dark-color);
}
