.heroBackground {
  background: radial-gradient(
    56.77% 244.59% at 71.15% 77.84%,
    var(--hero-background-light-color) 0%,
    var(--hero-background-middle-color) 43.79%,
    var(--hero-background-dark-color) 100%
  );
}

.profilePic {
  position: absolute;
  width: 35%;
  right: 2.22%;
  bottom: 0;
}

.introduction {
  position: absolute;
  left: 0;
  top: 50%;
  margin: auto 0;
  transform: translateY(-50%);
}

.introduction h1 {
  font-size: 96px;
  margin: 0;
}

.introduction h1,
.introduction h2 {
  font-weight: 500;
}
