nav {
  position: fixed;
  z-index: 100;
  width: 100%;
  transition: background-color 0.5s ease;
  padding-bottom: 4px;
}
#navContainer {
  width: 60%;
  margin: 0 20%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#navContainer a {
  color: var(--on-primary-color);
  text-decoration: none;
}

.navLogo {
  font-size: 48px;
}

.navLink {
  font-size: 24px;
  font-weight: 500;
  padding: 0 16px;
  position: relative;
  transition: color 0.5se ease-in-out;
}

.navLink:after {
  content: "";
  display: block;
  height: 4px;
  width: 0;
  margin: 0 10%;
  left: 0;
  bottom: -4px;
  background-color: var(--acent-color);
  position: absolute;
  border-radius: 10px;
  transition: width 0.5s ease;
}
.active.navLink:after {
  width: 80%;
}

#navContainer a.active.navLink {
  color: var(--acent-color);
}

nav.scrolled {
  background: rgb(0, 0, 0);
}
