.skillsDetails {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-between;
}

.skill {
  width: 49%;
  display: inline-flex;
  flex-direction: column;
  margin-bottom: 40px;
  box-sizing: border-box;
}

.skill:nth-child(2n + 1) {
  border-right: 4px solid var(--primary-dark-color);
}

.skill b {
  font-weight: 500;
  font-size: 36px;
  margin-bottom: 24px;
}

.skillsList {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.chip {
  font-size: 24px;
  padding: 4px 12px;
  margin: 8px;
  border: 4px solid;
  border-radius: 16px;
}

.chip.variant1 {
  color: var(--chip-color-1);
  border-color: var(--chip-color-1);
}

.chip.variant2 {
  color: var(--chip-color-2);
  border-color: var(--chip-color-2);
}

.chip.variant3 {
  color: var(--chip-color-3);
  border-color: var(--chip-color-3);
}
